import React, { Component } from 'react';
import PropTypes from 'prop-types';

class Welcome extends Component {
  render() {
    const tasks = this.props.tasks.length;

    return (
      // Check for presence tasks
      <div className={tasks === 0 ? '' : 'none'}>
        <div id="welcomeCard" className="row justify-content-center">
          <div className="col">
            <div
              className="jumbotron pt-0 pb-2 border mb-0"
              style={{ maxHeight: '300px', overflow: 'auto' }}
            >
              <div className="card mt-2">
                <div className="card-body">
                  <h5 className="card-title text-center">
                    You don't have saved tasks
                  </h5>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

Welcome.propTypes = {
  tasks: PropTypes.array.isRequired
};

export default Welcome;
